import { writable } from "svelte/store";
export const defaultFormState = {
    title: "",
    content: "",
    type: null,
    isNegative: false,
};
export const currentResolution = writable(Object.assign({}, defaultFormState));
export const resetForm = () => {
    currentResolution.set(Object.assign({}, defaultFormState));
};
export const formState = writable({
    loading: false,
    awaitingConfirmation: false,
});
export const resetFormState = () => {
    formState.set({
        loading: false,
        awaitingConfirmation: false,
    });
};
