export const RESOLUTION_STATES = {
    PRE_DRAFT: "pre-draft",
    // transition to when approved
    NOTICE: "notice",
    // transition to when notice period ends
    VOTING: "voting",
    // transition to when voting period ends
    ENDED: "ended",
};
export const getResolutionState = (resolution) => {
    if (resolution.approveTimestamp !== "0") {
        // TODO
        // we should check the resolution type and
        // derive the state based on the approveTimestamp + notice/voting period
        return RESOLUTION_STATES.NOTICE;
    }
    return RESOLUTION_STATES.PRE_DRAFT;
};
export const getEnhancedResolutions = (resolutions, $resolutionContractTypes) => resolutions.map((resolution) => {
    const state = getResolutionState(resolution);
    return Object.assign(Object.assign({}, resolution), { typeName: $resolutionContractTypes[Number(resolution.typeId)].name, state: getResolutionState(resolution), href: state === RESOLUTION_STATES.PRE_DRAFT
            ? `#/resolutions/${resolution.id}/edit`
            : `#/resolutions/${resolution.id}` });
});
